import React, {useContext, useState} from "react";
import { Swiper, SwiperSlide } from 'swiper/react';
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/free-mode";
import {Pagination, Navigation} from "swiper";
import ProductContext from "../Context/ProductContext";
import {
  Link
} from "react-router-dom"
import ScrollTopAction from './../Action/ScrollTopAction'

function SwiperBar(props){
  let [load, setLoad] = useState(false);
  let {state, action} = useContext(ProductContext);
  let product = state.product.filter((item, key)=>item.top10 == true && item.parentCat == props.cat).map((item, key)=><SwiperSlide key={key} >

    {/* loading */}
  <div className={`w-full my-3 rounded shadow-lg h-[400px] relative block overflow-hidden anim-right ${!load ? 'block' : 'hidden'}`}>
            
    <h6 className="bg-gray-200 p-2 rounded-full absolute top-2 right-2 text-white w-14 h-9"></h6>
    <div className="absolute bottom-5 left-1 right-1 mx-auto p-2 h-16 rounded bg-gray-200 w-[90%] text-white text-center">
        
    </div>
    </div>
  
  {/* loding compolited img */}
    <Link onClick={ScrollTopAction} to={`/product/${item.id}`} className={`w-full my-3 rounded shadow-lg h-[430px] md:h-[408px] relative block overflow-hidden ${load ? 'block' : 'hidden'}`}>
        <img src={`./img/${item.img}`} onLoad={()=>{setLoad(true)}} alt="" className="h-full w-full object-cover object-top rounded hover:scale-105 transition-all duration-300" />
        <h6 className="bg-gray-600 p-2 rounded-full w-fit absolute top-2 right-2 text-white text-base">${item.price}</h6>
        <div className="p-2 absolute bottom-0 left-0 h-fit p-2 bg-gray-900/90 w-full text-white rounded-b text-center bd">
          <h5 className="py-1 px-2 overflow-hidden whitespace-nowrap text-ellipsis text-lg">{item.title.toUpperCase()}</h5>
        </div>
      </Link>

  </SwiperSlide>);

    return(
        <div className="px-0 lg:px-4 xl:px-0">
          <div className="w-full h-[530px] md:h-[500px] rounded-0 lg:rounded relative mb-4">
            <img src="./img/vbbddndgnfgnfhmhm.jpg" alt="" className="w-full h-full object-cover rounded-0 lg:rounded object-center"/>
            <div className="flex flex-col absolute justify-center items-center top-0 left-0 w-full h-full bg-gradient-to-r from-gray-800/90 via-gray-700/90 to-gray-900/90 text-white text-2xl rounded-0 lg:rounded">
              
              <Swiper
        breakpoints={{
          640: {
            slidesPerView: 1,
            
          },
          768: {
            slidesPerView: 2.5,
            
          },
          1024: {
            slidesPerView: 3.5,
            
          },
          1280:{
            slidesPerView: 4,
          },
          1536:{
            slidesPerView: 4.5,
          }
        }}
        spaceBetween={30}
        pagination={{
          clickable: true,
          color:'white'
        }}
        navigation={true}
        modules={[Pagination, Navigation]}
        className="mySwiper indexswiper w-full h-full padding-swiper"
      >

        {product}
    
      </Swiper>
            </div>
          </div>
        </div>
    )
}

export default SwiperBar;