import React, {useEffect, Suspense} from "react";
import MoreArrow from "./Index/MoreArrow";
import SerachBox from "./Index/SearchBox";
import WomenSection from "./Index/WomenSection";
import MenSection from "./Index/MenSection";
import BottomCategoris from "./Index/BottomCategoris";
import TopBannerLoading from "./Index/Loading/TopBannerLoading";
import TopCategorisLoading from "./Index/Loading/TopCategorisLoading";
const TopBanner = React.lazy(()=>import("./Index/TopBanner"));
const TopCategoris = React.lazy(()=>import("./Index/TopCategoris"));

function IndexPage(props){
    useEffect(()=>{
        document.title = "HM Shop | Home";
    },[])
    return (
        <div className="mt-4 container mx-auto rounded flex flex-col">
        
        <Suspense fallback={<TopBannerLoading />}>
        <TopBanner />
        </Suspense>

        <Suspense fallback={<TopCategorisLoading />}>
        <TopCategoris />
        </Suspense>

        <MoreArrow />
        
       <SerachBox />
        
        <WomenSection />
        
        <MenSection />

        <BottomCategoris />
        
  </div>
    )
}

export default IndexPage;