import React, {useState} from "react";
import {
  Link,
} from "react-router-dom"
function SerachBox(props){
  let [word, setWord] = useState('');
  let scroll = ()=>{
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    
  })
}
    return(
        <div className="mb-8 flex justify-center px-4">
        <div className="w-full max-w-[700px] relative">
          <input onChange={(e)=>{setWord(e.target.value)}} value={word} onKeyDown={(e)=>{if(e.key == 'Enter'){
          document.querySelector("a.search-enter").click();
        }}} type="text" name="search" id="search" placeholder="Search..." className="outline-none w-full py-2 px-4 border-2 border-gray-400 rounded-sm text-gray-400 focus:border-gray-500 focus:text-gray-900  transition-all duration-200"/>
          <Link to={`/search/${word}`} onClick={scroll} className="search-enter material-icons absolute right-0 top-0 p-2 text-gray-400 transition-all duration-200">
            search
          </Link>
        </div>
       </div>
    )
}

export default SerachBox;