import React, {useContext} from "react";
import ProductContext from "../Context/ProductContext";
import Card from "./Card";
import SwiperBar from "./SweiperBar";
import {
  Link
} from "react-router-dom"
import ScrollTopAction from './../Action/ScrollTopAction'

function WomenSection(props){

  let {state, action} = useContext(ProductContext);

  let product = state.product.filter(item=>item.parentCat == 'women' && item.top10 == false).slice(0, 8).map((item, key)=><Card img={item.img} price={item.price} title={item.title} id={item.id} key={key} />);
    return(
        <>

        <div className="px-4  xl:px-0 ">
          <span className="bg-gray-700 text-white rounded-t py-2 px-4 w-fit h-fit">
            Women
          </span>
        </div>
        
        <div className="flex flex-wrap flex-col md:flex-row justify-center px-4  xl:px-0 md:justify-between">
          {product}
        </div>

        <Link onClick={ScrollTopAction} to={`/cat/women`} className="w-fit ml-4 xl:ml-0 text-center py-2 px-4 text-white bg-gray-700 text-lg rounded hover:bg-gray-800 mb-4 transition-all">Load More</Link>
        
        <SwiperBar cat='women' />

        </>
    )
}

export default WomenSection;