import React, {useContext} from "react";
import ProductContext from "../Context/ProductContext";
import {Link} from 'react-router-dom';
import ScrollTopAction from "../Action/ScrollTopAction";

function BottomCategoris(props){
  let {state, action} = useContext(ProductContext);
  let women = state.cat.women.map((item, key)=> <Link key={key} onClick={ScrollTopAction} to={`/cat/women/${item}`} className="my-2 border-b-[1px] py-2 px-4 border-white hover:border-b-[1px] hover:border-gray-600 transition-all duration-200">{item}</Link>);
  let men = state.cat.men.map((item, key)=> <Link key={key} onClick={ScrollTopAction} to={`/cat/men/${item}`} className="my-2 border-b-[1px] py-2 px-4 border-white hover:border-b-[1px] hover:border-gray-600 transition-all duration-200">{item}</Link>);
    return(
        <div className="px-0 lg:px-4 xl:px-0 overflow-hidden my-8">
          <div className="bg-white w-full rounded flex flex-row justify-between relative h-fit lg:h-fit"> 
            
            <div className="py-4 w-full h-full flex flex-col lg:flex-row justify-between lg:items-center">
              <div className="w-full flex lg:hidden text-black flex-col justify-center items-center h-full pt-10 pb-20">
                <h1 className="text-xl">HM Shop</h1>
                <h1 className="text-3xl border-t-2 border-gray-800 mt-2 pt-2">The Best For The Best</h1>
              </div>
              <div className="w-full lg:w-[25%] mb-8 lg:mb-0 h-full flex flex-col justify-center items-center text-black">
                <Link to={`/cat/women`} onClick={ScrollTopAction} className="text-xl border-b-2 border-gray-800 mb-4 w-3/4 text-center py-2">Women</Link>
                {women}
              </div>
              <div className="w-[50%] hidden lg:flex text-black flex-col justify-center items-center h-full py-20">
                <h1 className="text-xl">HM Shop</h1>
                <h1 className="text-3xl border-t-2 border-gray-800 mt-2 pt-2">The Best For The Best</h1>
              </div>
              <div className="w-full lg:w-[25%]  h-full flex flex-col justify-center items-center text-black">
                <Link to={`/cat/men`} onClick={ScrollTopAction} className="text-xl border-b-2 border-gray-800 mb-4 w-3/4 text-center py-2">Men</Link>
                {men}
              </div>
            </div>
          </div>
        </div>
    )
}

export default BottomCategoris;